import React, { lazy, Suspense } from "react";
import { graphql } from "gatsby";
//import Layout from "../components/layout";
import SEO from "../components/seo";
//import Banner from "../components/banner";
//import Blogs from "../components/blogs";
//import Contact from "../components/contact";

const Blogs = lazy(() => import('../components/blogs'));
const Banner = lazy(() => import('../components/banner'));
const Layout = lazy(() => import('../components/layout'));
const Contact = lazy(() => import('../components/contact'));

const renderLoader = () => <p>Carregando</p>;

const title = "{ The Life Coding }";
const IndexPage = ({ data }) => (
  <Suspense fallback={renderLoader()}>
  <Layout header="home">
    <SEO
      title={title}
      keywords={[`Donato William`, `Back-end`, `Developer`, `Web`, `Software`, `Java`]}
    />
    <Suspense fallback={renderLoader()}><Banner data={data.contentfulAboutMe}></Banner></Suspense>

    {data.contentfulSiteInformation.menus
      .filter(item => item === "Blogs")
      .map(t => {
        return <Suspense fallback={renderLoader()}><Blogs data={data.allContentfulBlogs}></Blogs></Suspense>;
      })}

    {data.contentfulSiteInformation.menus
      .filter(item => item === "Contact")
      .map(t => {
        return <Suspense fallback={renderLoader()}><Contact data={data.contentfulAboutMe.gmail}></Contact></Suspense>;
      })}
  </Layout>
  </Suspense>
);

export default IndexPage;

export const pageQuery = graphql`
  query AboutQuery {
    contentfulAboutMe {
      name
      photo {
        file {
          url
        }
        fluid {
          base64
          aspectRatio
          src
          srcSet
          srcWebp
          srcSetWebp
          sizes
        }
      }
      designation
      age
      facebook
      github
      gmail
      id
      instagram
      linkdin
      twitter
      location
      description {
        childMarkdownRemark {
          html
        }
      }
      bannerImage {
        fluid(maxWidth: 1500) {
          base64
          aspectRatio
          src
          srcSet
          srcWebp
          srcSetWebp
          sizes
        }
      }
      bannerList
    }
    allContentfulService {
      edges {
        node {
          title
          description {
            childMarkdownRemark {
              html
            }
          }
        }
      }
    }
    allContentfulBlogs(limit: 5, sort: {fields: createdAt, order: DESC}) {
      edges {
        node {
          title
          slug
          featureImage {
            fluid(maxWidth: 600) {
              base64
              aspectRatio
              src
              srcSet
              srcWebp
              srcSetWebp
              sizes
            }
          }
          createdAt
          updatedAt
        }
      }
    }
    allContentfulWorks {
      edges {
        node {
          siteName
          url
          image {
            fluid(maxWidth: 600) {
              base64
              aspectRatio
              src
              srcSet
              srcWebp
              srcSetWebp
              sizes
            }
          }
        }
      }
    }
    contentfulPhotos {
      photos {
        fluid(maxWidth: 600) {
          base64
          aspectRatio
          src
          srcSet
          srcWebp
          srcSetWebp
          sizes
        }
      }
    }
    contentfulSiteInformation {
      menus
    }
  }
`;
